angular.module('app.auth').controller('LoginController', LoginController);

function LoginController(Auth, $rootScope, $state, Analytics, $cordovaDialogs) {
  var vm = this;
  vm.submit = submit;

  init();

  function init() {
    Analytics.trackView('Login');
  }

  function submit(params) {
    Auth.login(params.phoneNumber)
      .success(response => {
        $rootScope.phoneNumber = params.phoneNumber;
        $state.go('activate');
      })
      .catch(error => {
          $cordovaDialogs.alert('Giriş yapılamıyor. Lütfen telefon numaranızı kontrol edip tekrar deneyin.', 'Bütçele', 'Tamam')
              .then(function() { /* callback success */ });
        });
  }
}