angular.module('app.core').config((DSProvider) => {
  DSProvider.defaults.defaultAdapter = 'DSLocalStorageAdapter';
})
.config(($provide) => {
  $provide.decorator('$state', ($delegate, $stateParams) => {
    $delegate.reload = () => {
      return $delegate.go($delegate.current, $stateParams, {
        reload: true,
        inherit: false,
        notify: true
      });
    };
    return $delegate;
  });
});