angular.module('app.tags').config(($stateProvider) => {
  $stateProvider

    .state('app.tags', {
      url: '/tags',
      views: {
        'menuContent': {
          templateUrl: 'tags/tags.template.html',
          controller: 'TagsController as vm',
          resolve: {
            tags: (Tag) => Tag.findAll()
          }
        }
      }
    });
});