angular.module('app.auth').config(($stateProvider, $urlRouterProvider) => {
  $stateProvider

    .state('login', {
      url: '/login',
      templateUrl: 'auth/login.template.html',
      controller: 'LoginController as vm',
      resolve: {
        loginControl: (Settings, $location) => {
          if (Settings.get('user')) {
            $location.path('/app/active');
          }
        }
      }
    })

    .state('activate', {
      url: '/activate',
      templateUrl: 'auth/activate.template.html',
      controller: 'ActivateController as vm'
    });
});