angular.module('app.core').factory('Notification', Notification);

function Notification($ionicPlatform, $cordovaLocalNotification, DS, _, Settings, $timeout) {
  return {
    add: add,
    setWeekly: setWeekly,
    setDaily: setDaily,
    skipToday: skipToday,
    setForBudgets: setForBudgets,
    setAll: setAll
  }

  function add(options) {
    if (!window.cordova) return;
    $ionicPlatform.ready(() => {
      $cordovaLocalNotification.hasPermission().then(() => {
        $cordovaLocalNotification.cancel(options.id).then(() => {
          $cordovaLocalNotification.add(options);
        });
      }).catch(() => {
        $cordovaLocalNotification.registerPermission().then(() => {
          add(options);
        });
      });
    });
  }

  function cancelAll(id) {
    if (!window.cordova) return;
    $ionicPlatform.ready(() => {
      $cordovaLocalNotification.cancelAll(id);
    });
  }

  function setAll() {
    if (!window.cordova) return;
    $ionicPlatform.ready(() => {
      $cordovaLocalNotification.cancelAll().then(() => {
        setWeekly();
        setForBudgets();
        setDaily();
      });
    });
  }

  function setWeekly() {
    var lastWeek = moment().startOf('day').hour(-4).day(7);
    var thisWeek = moment().startOf('day').hour(-4).day(7).add(1, 'w');

    if (!lastWeek.isBefore()) {
      lastWeek.add(-1, 'w');
      thisWeek.add(-1, 'w');
    }

    var nextWeek = moment(thisWeek);

    var query = {
      where: {
        createdAt: {
          '>': lastWeek.toDate()
        }
      }
    }

    $timeout(() => {
      DS.definitions.transaction.findAll(query).then((transactions) => {
        var total = _.reduce(transactions, (sum, transaction) => sum + transaction.amount, 0);

        if (total > 0) {
          // timeout for preventing ios crashes
          $timeout(() => {
            add({
              id: 0,
              date: thisWeek.toDate(),
              message: `Bu hafta toplamda ${total} TL harcadın, detaylar Bütçele'de...`,
              badge: 1
            });
          }, 50);

          nextWeek.add(1, 'w');
        }

        add({
          id: 1,
          date: nextWeek.toDate(),
          repeat: 'weekly',
          message: 'Seni özledik, bütçelerin de özledi, hedeflerini hatırlamaya ne dersin?',
          badge: 1
        });
      });
    }, 0);
  }

  function setForBudgets() {
    DS.definitions.budget.findActiveBudgets().then((budgets) => {
      budgets.forEach((budget) => {
        var notificationId = Settings.get(`notification-${budget.id}`);
        if (!notificationId) {
          notificationId = Math.floor(Math.random() * 100000);
          Settings.set(`notification-${budget.id}`, notificationId);
        }

        DS.definitions.budget.loadRelations(budget,["transaction"]).then((budget) => {
          var total = _.reduce(budget.transactions, (sum, transaction) => sum + transaction.amount, 0);
          var ratio = total / budget.amount;

          if ( ratio >= 0.9 && ratio < 1) {
            var message = `${budget.name} bütçen bitmek üzere. Detaylar için Bütçele’ye göz atabilirsin.`;
            var notificationPercent = 90;
          } else if ( ratio >= 0.75 ) {
            var message = `${budget.name} bütçenin %%75’ini harcadın. Detaylar için Bütçele’ye göz atabilirsin.`;
            var notificationPercent = 75;
          } else if ( ratio >= 0.5 ) {
            var message = `${budget.name} bütçenin %%50’sini harcadın. Detaylar için Bütçele’ye göz atabilirsin.`;
            var notificationPercent = 50;
          } else {
            return;
          }

          var lastNotification = Settings.get(`notification-${budget.id}-last`);

          if (lastNotification && lastNotification.percent > notificationPercent) {
            return;
          }

          var date = moment().startOf('day').hour(21);

          if (date.isBefore()) {
            date.add(1, 'd');
          }

          if (lastNotification && lastNotification.percent == notificationPercent && date.isAfter(lastNotification.date)) {
            return;
          } else {
            Settings.set(`notification-${budget.id}-last`, {
              date: date.toDate().getTime(),
              percent: notificationPercent
            });
          }

          add({
            id: notificationId,
            date: date.toDate(),
            message: message,
            badge: 1
          });
        });
      });
    });
  }

  function setDaily() {
    var date = moment().startOf('day').hour(20);

    if (date.isBefore() || Settings.get(`notification-skipped-${moment().startOf('day').toDate().getTime()}`)) {
      date.add(1, 'd');
    }

    add({
      id: 2,
      date: date.toDate(),
      repeat: 'daily',
      message: "Bütçele'den Hatırlatma: Bugün hiç harcama girmedin, kaydetmeyi unuttuğun harcaman olabilir mi? :)",
      badge: 1
    });
  }

  function skipToday() {
    Settings.set(`notification-skipped-${moment().startOf('day').toDate().getTime()}`, true);
    setDaily();
  }
}