angular.module('app.budgets').factory('Budget', Budget);

function Budget(DS, _, $q, Notification, moment) {
  var colorOptions = 5;

  var resource = DS.defineResource({
    name: 'budget',
    relations: {
      hasMany: {
        transaction: {
          localField: 'transactions',
          foreignKey: 'budgetId'
        }
      }
    },
    beforeCreate: (_, attrs, next) => {
      if(!attrs.startDate){
        attrs.startDate = moment().startOf('month').toDate().getTime();
        attrs.endDate = moment().endOf('month').toDate().getTime();
      }

      attrs.startDate = moment(attrs.startDate).startOf('day').toDate().getTime();
      attrs.endDate = moment(attrs.endDate).endOf('day').toDate().getTime();

      attrs.color = Math.floor(Math.random() * colorOptions) + 1;

      attrs.isActive = true;
      next(null, attrs);
    },

    afterCreate: (_, attrs, next) => {
      Notification.setAll();
      next(null, attrs);
    },

    afterUpdate: (_, attrs, next) => {
      Notification.setAll();
      next(null, attrs);
    },

    createOrUpdate: (budget) => {
      if(budget.id)
        return resource.update(budget.id, budget);
      else {
        return resource.create(budget);
      }
    },

    findActiveBudgets: () => resource.findAll({ where: { isActive: true } }),

    findPassiveBudgets: () => {
      var deferred = $q.defer();
      resource.findAll({ where: { isActive: false } }).then((budgets) => {
        resource.findActiveBudgets().then((activeBudgets) => {
          var activeBaseIds = activeBudgets.map((budget) => budget.baseId);
          var passiveBudgets = budgets.filter((budget) => {
            return !(_.include(activeBaseIds, budget.baseId) || _.include(activeBaseIds, budget.id));
          });
          var grouped = _.groupBy(passiveBudgets, (budget) => budget.baseId || budget.id);

          deferred.resolve(_.map(grouped, (budgets) => budgets[budgets.length - 1]));
        });
      });
      return deferred.promise;
    },

    findRelateds: (budget) => {
      var deferred = $q.defer();
      var baseId;
      var result = [];

      if (budget.baseId) {
        baseId = budget.baseId;
      } else {
        baseId = budget.id;
      }

      resource.findAll({ where: { baseId: baseId }}).then((budgets) => {
        resource.find(baseId).then((budget) => {
          deferred.resolve(budgets.reverse().concat(budget));
        });
      });

      return deferred.promise;
    },

    spentBudget: (actions) => {
      var spent = 0;
      for(var i=0; i < actions.length; i++){
        spent += actions[i].amount;
      }
      return spent;
    },

    remainingBudget: (budget,actions) => {
      var spent = 0;
      for(var i=0; i < actions.length; i++){
        spent += actions[i].amount;
      }
      return budget.amount - spent;
    },

    remainingTotal: (budget) => {
      var sum = _.reduce(budget.transactions, function(sum, transaction) {
        return sum + transaction.amount;
      }, 0);
      return budget.amount - sum;
    },

    getPercentage: (budget) => {
      var remaining = this.remainingTotal(budget);
      return Math.ceil((remaining / this.remainingTotal(budget) )*10)*10;
    },

    findDaysToFinish: (budgetEndDate) => {
      var today = new Date();
      today.setHours(0,0,0,0);
      today = today.getTime();

      return Math.round( (budgetEndDate - today)/(1000*60*60*24) );
    },

    findDaysOfDifference: (startDay, endDay) => {
      return Math.round((endDay - startDay) / (1000 * 60 * 60 * 24));
    }

  });

  return resource;
}