angular.module('app.auth').controller('ActivateController', ActivateController);

function ActivateController(Auth, Settings, $state, Analytics, $cordovaGoogleAnalytics, $ionicViewService, $cordovaDialogs, $rootScope) {
  var vm = this;
  vm.submit = submit;

  init();

  function init() {
    Analytics.trackView('Activation');
  }

  function submit(params) {
    authActivation(params);

    $ionicViewService.nextViewOptions({
      disableBack: true
    });
  }

  function authActivation(params){
    Auth.activate(params.phoneNumber, params.activationCode)
        .success(response => {
          Settings.set('user', response);
          if (window.cordova) $cordovaGoogleAnalytics.setUserId(response.id);
          $state.go('app.active');
        })
        .catch(e => {
          $cordovaDialogs.confirm('Aktivasyon kodunu yanlış girdiniz. Lütfen tekrar deneyin.', 'Bütçele', ['Tekrar gönder','İptal'])
            .then(function(buttonIndex) {
              if(buttonIndex == 1) {
                tryToActivate();
                params.activationCode = "";
              }
            });
        });
  }

  function tryToActivate() {
    Auth.login($rootScope.phoneNumber)
        .catch(error => {
          $cordovaDialogs.confirm('Aktivasyon kodunu gönderilemedi. Lütfen tekrar deneyin.', 'Bütçele', ['Tekrar gönder','İptal'])
              .then(function(buttonIndex) {
                // no button = 0, 'OK' = 1, 'Cancel' = 2
                if(buttonIndex == 1) {
                  tryToActivate();
                }
              });
        });
  }
}