angular.module('app.budgets').config(($stateProvider) => {
  $stateProvider

    .state('app.active', {
      url: '/active',
      views: {
        'menuContent': {
          templateUrl: 'budgets/budgets.template.html',
          controller: 'BudgetsController as vm',
          resolve: {
            budgets: (Budget) => Budget.findActiveBudgets(),
            budget: () => ({})
          }
        }
      }
    })
    .state('app.archive', {
      url: '/archive',
      views: {
        'menuContent': {
          templateUrl: 'budgets/archive.template.html',
          controller: 'ArchiveController as vm',
          resolve: {
            budgets: (Budget) => Budget.findPassiveBudgets()
          }
        }
      }
    })
    .state('app.new', {
      url: '/new',
      views: {
        'menuContent': {
          templateUrl: 'budgets/budget-form.template.html',
          controller: 'BudgetFormController as vm',
          resolve: {
            budget: () => ({})
          }
        }
      }
    })
    .state('app.show', {
      url: '/:id',
      views: {
        'menuContent': {
          templateUrl: 'budgets/budget.template.html',
          controller: 'BudgetController as vm',
          resolve: {
            budget: ($stateParams, Budget) => Budget.find($stateParams.id),
            transactions: ($stateParams, Transaction) => Transaction.findAll(
              { where: { budgetId: $stateParams.id }
            }),
            relateds: (Budget, budget) => Budget.findRelateds(budget)
          }
        }
      }
    })
    .state('app.transactions', {
      url: '/:id/detail',
      views: {
        'menuContent': {
          templateUrl: 'budgets/budget-transaction-list.template.html',
          controller: 'BudgetTransactionListController as vm',
          resolve: {
            budget: ($stateParams, Budget) => Budget.find($stateParams.id),
            transactions: ($stateParams, Transaction) => Transaction.findAll(
                { where: { budgetId: $stateParams.id }
                })
          }
        }
      }
    });
});