angular.module('app.transactions').factory('Transaction', Transaction);

function Transaction(DS, Notification, _) {
  var resource = DS.defineResource({
    name: 'transaction',
    relations: {
      belongsTo: {
        budget: {
          localKey: 'budgetId',
          localField: 'budget'
        },
        tag: {
          localKey: 'tagId',
          localField: 'tag'
        }
      }
    },

    createOrUpdate: (transaction) => {
      transaction = _.pick(transaction, 'id', 'budgetId', 'amount', 'tagId');

      if(transaction.id)
        return resource.update(transaction.id, transaction);
      else
        return resource.create(transaction);
    },

    beforeCreate: (_, attrs, next) => {
      attrs.createdAt = new Date().getTime();
      next(null, attrs);
    },

    afterCreate: (_, attrs, next) => {
      Notification.setAll();
      Notification.skipToday();
      next(null, attrs);
    },

    afterUpdate: (_, attrs, next) => {
      Notification.setAll();
      next(null, attrs);
    },

    afterDestroy: (_, attrs, next) => {
      Notification.setAll();
      next(null, attrs);
    }
  });

  return resource;
}