angular.module('app.core').factory('Settings', Settings);

function Settings(localStorageService) {
  var settings = localStorageService.get('settings') || {};

  return {
    get: get,
    set: set
  }

  function get(key) {
    return settings[key];
  }

  function set(key, value) {
    settings[key] = value;
    localStorageService.set('settings', settings);
  }
}